<style>
.box{
	width: 300px;
	height: 400px;
	margin: auto;
	transform: rotateX(30deg);
	transform-style: preserve-3d;
}
.box ul{
	width: 300px;
	height: 400px;
	position: relative;
	left: 150px;
	transform-style: preserve-3d;
	
}
.box ul li{
	width: 300px;
	height: 400px;
	position: absolute;
	transform-origin: left;
	transition: all 2s;
	cursor: pointer;
	box-shadow: 0 0 6.18px 6.18px palevioletred;
}
</style>

<template>
	<div class="box" @mousemove="handleMove($event)" @mouseout="handleOut">
		<ul>
			<li v-for="(item,index) in list" :key="index" :style="{'transform':`rotateY(${index*tranDeg}deg)`,'box-shadow':item.bShanow}">
				<img :src="`./static/img/${item.id}.jpg`" width="100%" height="100%" @mousedown="cannotDrag($event)" @click="shrinkBook">
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'App',
	data() {
		return {
			list: [],
			tranDeg: 0,
			shrink: 0
		}
	},
	computed:{
		
	},
	methods:{
		handleMove(e){
			this.tranDeg = e.clientX / 10;
			if(this.tranDeg>0){
				this.tranDeg = 0;
			}	
		},

		randomColor(){
			let [r,g,b] = [Math.floor(Math.random()*256),Math.floor(Math.random()*256),Math.floor(Math.random()*256)];
			return `rgb(${r},${g},${b})`
		},

		handleOut() {
			this.tranDeg = -(180/(this.list.length-1));
		},

		shrinkBook() {
			this.shrink++;
			if(this.shrink%2 !=0){
				this.tranDeg = 0;
			}else{
				this.tranDeg = -(180/(this.list.length-1));
			}
		},

		cannotDrag(e){
			e.preventDefault();
		},
	},
	created() {
		for(let i=1;i<=11;i++){
			this.list.push({
				id:i,
				bShanow:`0 0 6.18px 6.18px ${this.randomColor()}`
			});
		}
	},
	mounted() {
		setTimeout(()=>{
			this.tranDeg = -(180/(this.list.length-1));
		},240);
		
	}
}
</script>





